.header {
  position: relative;
  background: $violet;
  padding: 24px 0 10px;
  margin-bottom: 90px;
  @include md {
    margin-bottom: 54px; }
  @include sm {
    background-image: linear-gradient(to bottom, #2B2839, rgba(#6B73B3, .34));
    padding: 18px 0 12px; }
  &__in {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between; }
  &__logo {
    margin-right: 100px;
    @include md {
      margin-right: 40px; } }
  &__link {
    color: #fff;
    @include tr(color .3s ease-in-out);
    @include sm {
      display: block;
      text-align: center;
      line-height: 126px;
      &:hover {
        background: #3B3C5E; } }
    &:not(:last-child) {
      margin-right: 35px;
      @include sm {
        margin-right: 0; } }
    .icon-question {
      color: $green; }
    .icon-conversation {
      color: $pink; }
    .icon {
      display: inline-block;
      vertical-align: middle;
      width: 30px;
      height: 30px;
      margin-right: 15px; }
    > span {
      display: inline-block;
      vertical-align: middle; } }
  &__link_green {
    &:hover {
      color: $green; } }
  &__link_pink {
    &:hover {
      color: $pink; } }
  &__left {
    .header__link {
      @include sm {
        display: none; } } }
  &__right_hidden {
    @include rmin(767) {
      text-align: center;
      display: none; } }
  &__right {

    &.is-open {
      @include sm {
        display: block; } }

    @include sm {
      display: none;
      position: absolute;
      top: 100%;
      height: calc(100vh - 104px);
      left: 0;
      width: 100%;
      z-index: 99;
      text-align: center;
      background: linear-gradient(to bottom, #2B2839, #2E335E); } } }

.burger {
  @include rmin(767) {
    display: none; } }
